<template>
  <div>
    <router-view class="chop__content"></router-view>
    <van-tabbar v-model="active"
                @change="goTarget"
                active-color="#ee0a24"
                inactive-color="#000">
      <van-tabbar-item icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item icon="qr">分类</van-tabbar-item>
      <van-tabbar-item icon="shopping-cart-o "
                       :badge="countCart">购物车</van-tabbar-item>
      <van-tabbar-item icon="contact">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'index',
  data () {
    return {
      active: 0
    }
  },
  computed: {
    countCart () {
      let count = 0
      for (const key in this.cartGoodsData) {
        count += this.cartGoodsData[key].num
      }
      return count
    },
    ...mapState(['cartGoodsData'])
  },
  methods: {
    goTarget (active) {
      let target = null
      switch (active) {
        case 0:
          target = { name: 'home' }
          break
        case 1:
          target = { name: 'classify' }
          break
        case 2:
          target = { name: 'cart' }
          break
        case 3:
          target = { name: 'mine' }
      }
      this.$router.push(target)
    }
  }
}
</script>
<style lang="scss" scoped>
a {
  font: 1em sans-serif;
  color: rgba(146, 128, 128, 0.849);
}
</style>
