import { ADD_GOODS_DATA } from './mutation-types'
import { addCartGoods } from '../localStorage/cartGoods'
import Vue from 'vue'
export default {
  [ADD_GOODS_DATA] (state, goods) {
    console.log(goods, state)
    // state.cartGoodsData = {
    //   ...state.cartGoodsData,
    //   [goods.goodsId]: {
    //     ...goods
    //   }
    // }

    // const goodsItem = {
    //   [goods.goodsId]: {
    //     ...goods
    //   }
    // }
    // state.cartGoodsData = Object.assign({}, goodsItem, state.cartGoodsData)
    const flag = state.cartGoodsData[goods.goodsId]
    if (flag) {
      state.cartGoodsData[goods.goodsId].num += 1
    } else {
      // state.cartGoodsData[goods.goodsId] = goods
      Vue.set(state.cartGoodsData, goods.goodsId, goods)
    }
    // Vue.set(state.cartGoodsData, goods.goodsId, goods)
    addCartGoods(state)
  }
}
