(function () {
  // 设计稿750px html font-size设成设成100px
  setRem()
  function setRem () {
    const design = 750
    const turnDesignRem = design / 100
    const cw = document.documentElement.clientWidth

    document.documentElement.style.fontSize = cw / turnDesignRem + 'px'
  }
  window.addEventListener('resize', function () {
    setRem()
  }, false)
})()
